/* Loading Screen */
.loading-cont {
  width: 100%;
  height: 100vh;
  display: flex;
  background-color: white;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.loading-cont p {
  color: #096b3d;
  font-size: 3rem;
  position: absolute;
  bottom: 10rem;
}

/* Spinner */

.loader {
  width: 48px;
  height: 48px;
  border: 5px solid #82b834;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  /* margin: 4rem auto; */
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

