/* modal styles */
.backdrop {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.backdrop2 {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vh;
  height: 100vh;
  background: rgba(0, 0, 0, 0.25);
}

.backdrop img {
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  border: 3px solid white;
}

.sun-m {
  transform: scale(0);
  animation: sun-m 1.5s ease-out 1.2s;
  animation-fill-mode: forwards;
}

#Rectangle_18 {
  fill: #546ec7;
  animation: sky 1.5s ease 1.2s;
  animation-fill-mode: forwards;
}

.cloud {
  animation: cloud1 20s ease infinite 10s;
  transform: translate(49px, 59px);
}

.cloud2 {
  animation: cloud2 30s ease infinite;
  transform: translate(-17px, 6px);
}

.cloud3 {
  animation: cloud3 33s ease infinite 8s;
  transform: translate(2px, -48px);
}

.cloud4 {
  animation: cloud4 36s ease infinite 12s;
  transform: translate(-15px, 43px);
}

.mcloud1 {
  animation: mcloud1 30s ease infinite;
}

.mcloud2 {
  animation: mcloud2 26s ease infinite;
}

.mcloud3 {
  animation: mcloud3 30s ease infinite;
}

.mcloud4 {
  animation: mcloud4 38s ease infinite;
}

@keyframes homesvg {
  0% {
    /* transform: translateX(100px); */

    opacity: 0;
  }
  100% {
    opacity: 1;

    /* transform: translateX(0); */
  }
}

@keyframes mobilesvg {
  0% {
    /* transform: translateY(-100px); */
    opacity: 0;
  }
  100% {
    opacity: 1;
    /* transform: translateY(0); */
  }
}

@keyframes sun-m {
  0% {
    transform: translate(0px, 140px) scale(1);
  }
  100% {
    transform: translate(0px, 0px) scale(1);
  }
}

@keyframes sky {
  0% {
    fill: #546ec7;
  }
  100% {
    fill: #9bebe7;
  }
}

@keyframes cloud1 {
  0% {
    transform: translate(49px, 59px);
  }
  50% {
    transform: translate(10px, 59px);
  }
  100% {
    transform: translate(49px, 59px);
  }
}

